export default "/* Global */\n* {\n    box-sizing: border-box;\n}\nbody,\nhtml {\n    height: 100%;\n    min-height: 100%;\n}\nbody {\n    font-family: 'Roboto',\n    sans-serif;\n    margin: 0;\n    background-color: #e7e7e7;\n}\n/* Credit Card */\n.credit-card {\n    width: 360px;\n    height: 400px;\n    margin: 60px auto 0;\n    border: 1px solid #ddd;\n    border-radius: 6px;\n    background-color: #fff;\n    box-shadow: 1px 2px 3px 0 rgba(0,0,0,.10);\n}\n.form-header {\n    height: 60px;\n    padding: 20px 30px 0;\n    border-bottom: 1px solid #e1e8ee;\n}\n.form-body {\n    height: 340px;\n    padding: 30px 30px 20px;\n}\n/* Title */\n.title {\n    font-size: 18px;\n    margin: 0;\n    color: #5e6977;\n}\n/* Common */\n.card-number,\n.cvv-input input,\n.month select,\n.year select {\n    font-size: 14px;\n    font-weight: 100;\n    line-height: 14px;\n}\n.card-number,\n.month select,\n.year select {\n    font-size: 14px;\n    font-weight: 100;\n    line-height: 14px;\n}\n.card-number,\n.cvv-details,\n.cvv-input input,\n.month select,\n.year select {\n    opacity: .7;\n    color: #86939e;\n}\n/* Card Number */\n.card-number {\n    width: 100%;\n    margin-bottom: 20px;\n    padding-left: 20px;\n    border: 2px solid #e1e8ee;\n    border-radius: 6px;\n}\n/* Date Field */\n.month select,\n.year select {\n    width: 145px;\n    margin-bottom: 20px;\n    padding-left: 20px;\n    border: 2px solid #e1e8ee;\n    border-radius: 6px;\n    /* background: ; */\n    background-position: 85% 50%;\n    -moz-appearance: none;\n    -webkit-appearance: none;\n}\n.month select {\n    float: left;\n}\n.year select {\n    float: right;\n}\n/* Card Verification Field */\n.cvv-input input {\n    float: left;\n    width: 145px;\n    padding-left: 20px;\n    border: 2px solid #e1e8ee;\n    border-radius: 6px;\n    background: #fff;\n}\n/* Buttons Section */\n.paypal-btn,\n.proceed-btn {\n    cursor: pointer;\n    font-size: 16px;\n    width: 100%;\n    border-color: transparent;\n    border-radius: 6px;\n}\n.proceed-btn {\n    margin-bottom: 10px;\n    background: #7dc855;\n}\n.paypal-btn a,\n.proceed-btn a {\n    text-decoration: none;\n}\n.proceed-btn a {\n    color: #fff;\n}\n.paypal-btn a {\n    color: rgba(242, 242, 242, .7);\n}\n.paypal-btn {\n    padding-right: 95px;\n    /* background: url('paypal-logo.svg') no-repeat 65% 56% #009cde; */\n}\n.cvv-details {\n    font-size: 12px;\n    font-weight: 300;\n    line-height: 16px;\n    float: right;\n    margin-bottom: 20px;\n}\n.cvv-details p {\n    margin-top: 6px;\n}\n/*# sourceMappingURL=data:application/json;base64, */"